<template>
  <div class="seller-goods-add" style="padding-top: 44px;">
    <backer-header title="添加商品" />
    <scroll ref="scroll" clickable>
      <div @click="$router.push('/seller/goods/classify')" class="select">
        <p>商品类别</p>
        <span :class="{active: goodsClassify.title}">{{ goodsClassify.title || '请选择' }} <van-icon name="arrow" /></span>
      </div>
      <div class="goods-section">
        <h4>商品标题</h4>
        <input @blur.prevent="inputBlur" v-model="form.title" type="text" placeholder="建议填写产品词、属性词、营销词等">
        <div class="covers">
          <div v-for="(item, index) in titleImgs" :key="item + index" :style="{backgroundImage: `url(${item})`}" class="img">
            <img @click="delTitleImgs(index)" src="./delete.png" alt="">
          </div>
          <img @click="clickFile('title')" src="./pic@2x.png" alt="">
        </div>
      </div>
      <div class="goods-section">
        <h4>商品详情</h4>
        <textarea @blur.prevent="inputBlur" v-model="form.desc" placeholder="建议通过细节说明、规格、搭配、购买须知等多方面描述商品的详情" />
        <div class="covers">
          <div v-for="(item, index) in detailImgs" :key="item + index" :style="{backgroundImage: `url(${item})`}" class="img">
            <img @click="delDetailImgs(index)" src="./delete.png" alt="">
          </div>
          <img @click="clickFile('detail')" src="./xiangqing@2x.png" alt="">
        </div>
      </div>
      <div class="classify">
        <h4>颜色分类 <img @click="pushColor" src="./xinzen@2x.png" alt=""></h4>
        <div v-for="(item, index) in colors" :key="item.key" class="color">
          <img @click="clickFile('color', index)" class="img" v-if="!item.img_url" src="./color@2x.png" alt="">
          <van-image class="img" v-else :src="item.img_url" />
          <input @blur.prevent="inputBlur" @click="editColor(index)" v-model="item.color" type="text" placeholder="请输入颜色分类">
          <img v-if="item.isConfirm" @click="delColor(index)" class="del" src="./del@2x.png" alt="">
          <button v-else @click="confirmColor(index)">确定</button>
        </div>
      </div>
      <div class="classify">
        <h4>尺码分类 <img @click="pushSize" src="./xinzen@2x.png" alt=""></h4>
        <div v-for="(item, index) in sizes" :key="item.key" class="color size">
          <input @blur.prevent="inputBlur" v-model="item.size" type="text" placeholder="请输入尺码">
          <img v-if="item.isConfirm" @click="delSize(index)" class="del" src="./del@2x.png" alt="">
          <button @click="confirmSize(index)" v-else>确定</button>
        </div>
      </div>
      <div class="rules">
        <h4>销售规格</h4>
        <div v-for="(item, index) in rules" :key="index" class="item">
          <h5><span>{{ item.color }}</span><span>{{ item.size }}</span></h5>
          <div class="input">
            <label>价格（元）</label>
            <input @blur.prevent="inputBlur" v-model="item.price" type="text" placeholder="请输入商品价格">
          </div>
          <div class="input">
            <label>库存（件）</label>
            <input @blur.prevent="inputBlur" type="text" v-model="item.left_count" placeholder="请输入商品库存">
          </div>
        </div>
      </div>
      <div class="address" @click="showPicker=true">
        <label>发货地</label>
        <span :class="{active: address}">{{ address || '请选择发货地' }} <van-icon name="arrow" /></span>
      </div>
      <div class="address">
        <label>发货周期（小时）</label>
        <input @blur.prevent="inputBlur" v-model="form.delivery" type="text" placeholder="请输入发货时长">
      </div>
      <div class="freight">
        <div class="title">
          <h6>运费设置</h6>
          <p><span>包邮</span> <van-switch size="0.5rem" disabled inactive-color="#9D9D9D" active-color="#5ED247" v-model="checked" /></p>
        </div>
        <input v-if="!checked" type="text" v-model="form.freight" placeholder="请输入运费（元）">
      </div>
      <div class="bottom-btn" @click="submit">提交审核</div>
    </scroll>
    <input type="file" @change="upload" ref="file" v-show="false">
    <van-popup v-model="showPicker" round position="bottom">
      <van-picker
        :swipe-duration="500"
        show-toolbar
        value-key="name"
        :columns="columns"
        @cancel="showPicker = false"
        @confirm="onConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
import BackerHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import { getItem, removeItem } from '../../common/js/storage'
import cos from '../../common/js/cos'
import { mapState } from 'vuex'
import cloneDeep from '../../common/js/clone-deep'
import { post } from '../../common/js/http'
import location from '../../common/js/address'

export default {
  name: 'seller-goods-add',
  components: {
    BackerHeader,
    Scroll
  },
  data() {
    return {
      titleImgs: [],
      upType: 'title',
      detailImgs: [],
      colors: [],
      sizes: [],
      checked: true,
      goodsClassify: {},
      rules: [],
      imgIndex: 0,
      form: {
        title: '',
        desc: '',
        delivery: '',
        freight: 0
      },
      address: '',
      columns: location,
      showPicker: false
    }
  },
  created() {
    this.init()
  },
  activated() {
    this.init()
    window.addEventListener('resize', this.scrollRefresh)
  },
  mounted() {
    window.addEventListener('resize', this.scrollRefresh)
  },
  deactivated() {
    window.removeEventListener('resize', this.scrollRefresh)
  },
  computed: {
    ...mapState(['appConfig', 'routeHistory'])
  },
  methods: {
    inputBlur () {
      const u = navigator.userAgent
      const isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
      if (isIOS) {
        setTimeout(() => {
          const scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0
          window.scrollTo(0, Math.max(scrollHeight - 1, 0))
        }, 200)
      }
    },
    scrollRefresh() {
      this.$refs.scroll.refresh()
    },
    onConfirm(val) {
      this.address = val.join('')
      this.showPicker = false
    },
    init() {
      const classify = getItem('selectClassify')
      const address = getItem('selectAddress')
      if (classify) {
        this.goodsClassify = classify
        removeItem('selectClassify')
      }
      if (address) {
        this.address = address
        removeItem('selectAddress')
      }
    },
    delTitleImgs(index) {
      this.titleImgs.splice(index, 1)
    },
    delDetailImgs(index) {
      this.detailImgs.splice(index, 1)
    },
    delColor(index) {
      const rules = cloneDeep(this.rules)
      for (let i = 0; i < rules.length; i++) {
        if (rules[i].color === this.colors[index].color) {
          rules.splice(i, 1)
          i--
        }
      }
      this.rules = rules
      this.colors.splice(index, 1)
      this.$nextTick(() => {
        this.scrollRefresh()
      })
    },
    delSize(index) {
      const rules = cloneDeep(this.rules)
      for (let i = 0; i < rules.length; i++) {
        if (this.sizes.length === 1) {
          rules[i].size = ''
        } else {
          if (rules[i].size === this.sizes[index].size) {
            rules.splice(i, 1)
            i--
          }
        }
      }
      // this.genRules()
      this.rules = rules
      this.sizes.splice(index, 1)
      this.$nextTick(() => {
        this.scrollRefresh()
      })
    },
    clickFile(type, index = 0) {
      this.upType = type
      this.imgIndex = index
      this.$refs.file.click()
    },
    editColor(index) {
      this.$set(this.colors, index, {
        ...this.colors[index],
        isConfirm: false
      })
    },
    pushColor() {
      this.colors.push({
        key: Date.now(),
        img_url: '',
        isConfirm: '',
        color: '',
        prev: ''
      })
      this.$nextTick(() => {
        this.scrollRefresh()
      })
    },
    pushSize() {
      this.sizes.push({
        key: Date.now(),
        size: '',
        isConfirm: '',
        prev: ''
      })
      this.$nextTick(() => {
        this.scrollRefresh()
      })
    },
    confirmColor(index) {
      if (!this.colors[index].color) {
        this.$toast.fail('请输入颜色分类')
        return
      }
      for (let i = 0; i < this.colors.length; i++) {
        if (i === index) continue
        if (this.colors[i].color === this.colors[index].color) {
          this.$toast.fail('颜色分类不能重复')
          return
        }
      }
      this.genRules(this.colors[index], 'color', this.colors[index].prev)
      this.$set(this.colors, index, {
        ...this.colors[index],
        isConfirm: true,
        prev: this.colors[index].color
      })
    },
    confirmSize(index) {
      if (!this.sizes[index].size) {
        this.$toast.fail('请输入尺码分类')
        return
      }
      for (let i = 0; i < this.sizes.length; i++) {
        if (i === index) continue
        if (this.sizes[i].size === this.sizes[index].size) {
          this.$toast.fail('尺码分类不能重复')
          return
        }
      }
      this.genRules(this.sizes[index], 'size', this.sizes[index].prev)
      this.$set(this.sizes, index, {
        ...this.sizes[index],
        isConfirm: true,
        prev: this.sizes[index].size
      })
    },
    genRules(current, type, prev) {
      const rules = cloneDeep(this.rules)
      if (prev) {
        rules.forEach(item => {
          if (type === 'size' && item.size === prev) {
            item.size = current.size
          }
          if (type === 'color' && item.color === prev) {
            item.color = current.color
          }
        })
        this.rules = rules
        return
      }
      if (type === 'color') {
        const sizes = this.sizes.filter(item => item.isConfirm)
        if (!sizes.length) {
          rules.push({
            color: current.color,
            size: '',
            left_count: '',
            price: ''
          })
        } else {
          sizes.forEach(size => {
            rules.push({
              color: current.color,
              size: size.size,
              left_count: '',
              price: ''
            })
          })
        }
      } else {
        const colors = this.colors.filter(item => item.isConfirm)
        if (!colors.length) return
        const repeat = rules.map(item => {
          if (!item.size) {
            item.size = current.size
            return item.color
          } else {
            return ''
          }
        })
        colors.forEach(color => {
          if (repeat.indexOf(color.color) >= 0) return
          rules.push({
            color: color.color,
            size: current.size,
            left_count: '',
            price: ''
          })
        })
      }
      this.rules = rules
    },
    upload() {
      const file = this.$refs.file.files[0]
      if (!file) return
      this.cosImg(file)
        .then(url => {
          switch (this.upType) {
            case 'title':
              this.titleImgs.push(url)
              break
            case 'detail':
              this.detailImgs.push(url)
              break
            case 'color':
              this.$set(this.colors, this.imgIndex, {
                ...this.colors[this.imgIndex],
                img_url: url
              })
              break
          }
          this.$refs.file.value = ''
        })
    },
    cosImg(file) {
      const loading = this.$toast.loading({
        message: '上传中...'
      })
      return new Promise((resolve, reject) => {
        const cosObj = cos()
        cosObj.putObject({
          Bucket: this.appConfig.cos_bucket,
          Region: this.appConfig.cos_region,
          Key: this.appConfig.cos_folder_image + `/${file.name}`,
          Body: file
        }, (err, data) => {
          if (err) {
            this.$toast.fail('上传失败')
            reject(err)
            return
          }
          loading.close()
          resolve('https://' + data.Location)
        })
      })
    },
    submit() {
      let msg = ''
      if (!this.goodsClassify.id) msg = '请选择商品类别'
      if (!this.form.title && !msg) msg = '请输入商品标题'
      if (!msg && !this.titleImgs.length) msg = '请上传标题图片'
      if (!msg && !this.form.desc) msg = '请输入商品详情'
      if (!msg && !this.colors.filter(item => item.isConfirm).length) msg = '请输入商品颜色'
      const colorMap = {}
      this.colors.some(item => {
        if (!colorMap[item.color]) {
          colorMap[item.color] = 1
        } else {
          colorMap[item.color]++
        }
        if (colorMap[item.color] > 1) {
          if (!msg) {
            msg = '存在相同的颜色'
          }
        }
      })
      this.rules.some(item => {
        if (!item.price) {
          if (!msg) {
            msg = '有价格未输入'
          }
        }
        if (item.price.indexOf('.') >= 0) {
          if (!msg) {
            msg = '价格不能有小数'
          }
        }
        if (!item.left_count) {
          if (!msg) {
            msg = '有库存未输入'
          }
        }
      })
      if (!this.form.delivery && !msg) {
        msg = '请输入发货周期'
      }
      if (!this.address && !msg) {
        msg = '请输入发货地'
      }
      if (!this.checked && !this.form.freight && !msg) {
        msg = '请输入运费'
      }
      if (msg) {
        this.$toast.fail(msg)
        return
      }
      post('/shop/goods/publishGoods', {
        ...this.form,
        categoryid: this.goodsClassify.id,
        thumb_urls: this.titleImgs.join(','),
        desc_img_urls: this.detailImgs.join(','),
        colors: this.colors.filter(item => item.isConfirm),
        sizes: this.sizes.filter(item => item.isConfirm),
        inventorys: this.rules,
        address: this.address.province + this.address.city
      })
        .catch(err => {
          if (err.message === '发布成功，请等待管理员审核') {
            this.$router.goBack()
          }
        })
    }
  },
  watch: {
    routeHistory(newVal) {
      if (newVal.indexOf('/seller/goods/add') < 0) {
        this.$destroy()
      }
    }
  }
}
</script>

